h1{
    background-color: rgb(255,255,0);
    margin: 0 0 30px;
    padding: 15px 0;
    text-align: center;
    font-family: sans-serif;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-bottom: 3px solid black;
}

.container-tasks{
    display: flex;
    justify-content: center;
    font-size: 12px;
    font-family: sans-serif;
}

.container-active{
    width: 40%;
    margin-right: 20px;
    border-radius: 10px;
}

strong{
    font-size: 2em;
}

h3{
    text-align: center;
    color: black;
    font-size: 3em;
    padding: 10px 0;
    margin: 0;
    border-radius: 10px;
    background-color: rgb(255,150,150);
}

.container-done h3{
    background-color: rgb(150,255,150);
}

.container-done{
    width: 40%;
    margin-left: 20px;
    border-radius: 10px;
}

.tasks{
    border: 3px solid rgb(255,150,150);
    border-top: none;
    border-radius: 10px;
    margin-top: -20px;
    padding: 40px 20px 20px;
}

.container-done .tasks{
    border: 3px solid rgb(150,255,150);
    border-top: none;
}

.filter{
    text-align: center;
}

.filter p{
    display: inline-block;
    margin-right: 10px;
}

.filter button{
    display: inline;
    margin-right: 10px;
    height: 33px;
}

.container-done .filter button{
    border: 2px solid rgb(150,255,150);
}

button span{
    background-color: transparent;
    color: black;
    font-weight: bold;
    margin-left: 5px;
    font-size: 15px;
    display: inline-block;
    width: 15px;
    text-align: center;
    line-height: 18px;
}

@media (max-width: 1280px){
    .container-active{
        width: 48%;
        margin-right: 1%;
    }
    .container-done{
        width: 48%;
        margin-left: 1%;
    }
}

@media (max-width: 1100px){
    .task, .doneTask{
        flex-wrap: wrap;
        justify-content: center;
    }
    .text, .doneText{
        width: 100%;
    }
    button{
        height: 100%;
        margin-bottom: 10px;
    }
    .task::after {
        width: 90%;
    }
    .container-tasks{
        font-size: 10px;
    }
    h3{
        font-size: 25px;
    }
}

@media (max-width: 700px){
    .addTask{
        margin-bottom: 0;
        width: 80%;
        min-width: auto;
    }
    .container-tasks{
        flex-wrap: wrap;
    }
    .container-active, .container-done{
        width: 80%;
        margin: 10px 0;
    }
}

@media (max-width: 460px){
    .addTask{
        width: 94%;
    }
    .container-active, .container-done{
        width: 94%;
    }
}

@media (max-width: 360px){
    .addTask h2{
        font-size: 30px;
    }

    h3{
        font-size: 20px;
    }
}

@media (max-width: 310px){
    input{
        margin: 5px 30px;
    }
}