body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.addTask{
    position: relative;
    left: 50%;
    width: 40%;
    text-align: center;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-radius: 10px;
    font-size: 12px;
    font-family: sans-serif;
    min-width: 500px;
}

.date{
    display: block;
    margin-top: 10px;
}

.addTask h2{
    background-color: rgb(150, 150, 255);
    font-size: 3em;
    padding: 10px 0;
    margin: 0;
    border-radius: 10px;
}

.inputs{
    border: 3px solid rgb(150, 150, 255);
    border-radius: 10px;
    margin: -20px 0 0 0;
    padding: 40px 10px 20px;
}

input{
    border: 2px solid rgb(150, 150, 255);
    border-radius: 5px;
    margin: 0 10px;
    padding: 5px 5px;
    width: 150px;
}

#important{
    margin: 0 5px 0 0;
    width: auto;
}

.addTask button{
    border: 2px solid rgb(150, 150, 255);
    background-color: rgb(150, 150, 255);
    padding: 5px 10px;
    position: relative;
    margin-top: 10px;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}

.red{
    color: red;
    font-weight: bold;
}
.task{
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-bottom: 5px;
    padding-bottom: 5px;
}

.task::after{
    position: absolute;
    content: '';
    display: block;
    bottom: 0;
    left: 50%;
    height: 1px;
    width: 40%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    background-color: rgb(255, 150, 150);
}

.task:last-child::after{
    display: none;
}

.done.task::after{
    background-color: rgb(150, 255, 150);
}

.text{
    width: calc(100% - 225px);
    word-wrap: break-word;
}

.doneText{
    width: calc(100% - 90px);
    word-wrap: break-word;
}

.buttons, .doneButtons{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    width: 225px;
    max-height: 80px;
}

.doneButtons{
    width: 90px;
}

button{
    display: block;
    font-size: 1em;
    height: 40%;
    padding: 5px;
    background-color: transparent;
    border: 2px solid rgb(255, 150, 150);
    border-radius: 3px;
    outline: 0;
}

button:hover{
    cursor: pointer;
    border: 2px solid black;
    transition: .2s linear;
}

.delete{
    background-color: rgb(255, 150, 150);
}

.doneDelete{
    background-color: rgb(150, 255, 150);
    border: 2px solid rgb(150, 255, 150);
}

.noTask{
    font-size: 1.5em;
    text-align: center;
}

h1{
    background-color: rgb(255,255,0);
    margin: 0 0 30px;
    padding: 15px 0;
    text-align: center;
    font-family: sans-serif;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-bottom: 3px solid black;
}

.container-tasks{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: 12px;
    font-family: sans-serif;
}

.container-active{
    width: 40%;
    margin-right: 20px;
    border-radius: 10px;
}

strong{
    font-size: 2em;
}

h3{
    text-align: center;
    color: black;
    font-size: 3em;
    padding: 10px 0;
    margin: 0;
    border-radius: 10px;
    background-color: rgb(255,150,150);
}

.container-done h3{
    background-color: rgb(150,255,150);
}

.container-done{
    width: 40%;
    margin-left: 20px;
    border-radius: 10px;
}

.tasks{
    border: 3px solid rgb(255,150,150);
    border-top: none;
    border-radius: 10px;
    margin-top: -20px;
    padding: 40px 20px 20px;
}

.container-done .tasks{
    border: 3px solid rgb(150,255,150);
    border-top: none;
}

.filter{
    text-align: center;
}

.filter p{
    display: inline-block;
    margin-right: 10px;
}

.filter button{
    display: inline;
    margin-right: 10px;
    height: 33px;
}

.container-done .filter button{
    border: 2px solid rgb(150,255,150);
}

button span{
    background-color: transparent;
    color: black;
    font-weight: bold;
    margin-left: 5px;
    font-size: 15px;
    display: inline-block;
    width: 15px;
    text-align: center;
    line-height: 18px;
}

@media (max-width: 1280px){
    .container-active{
        width: 48%;
        margin-right: 1%;
    }
    .container-done{
        width: 48%;
        margin-left: 1%;
    }
}

@media (max-width: 1100px){
    .task, .doneTask{
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        -webkit-justify-content: center;
                justify-content: center;
    }
    .text, .doneText{
        width: 100%;
    }
    button{
        height: 100%;
        margin-bottom: 10px;
    }
    .task::after {
        width: 90%;
    }
    .container-tasks{
        font-size: 10px;
    }
    h3{
        font-size: 25px;
    }
}

@media (max-width: 700px){
    .addTask{
        margin-bottom: 0;
        width: 80%;
        min-width: auto;
    }
    .container-tasks{
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
    }
    .container-active, .container-done{
        width: 80%;
        margin: 10px 0;
    }
}

@media (max-width: 460px){
    .addTask{
        width: 94%;
    }
    .container-active, .container-done{
        width: 94%;
    }
}

@media (max-width: 360px){
    .addTask h2{
        font-size: 30px;
    }

    h3{
        font-size: 20px;
    }
}

@media (max-width: 310px){
    input{
        margin: 5px 30px;
    }
}
